<template>
    <div class="container">
        <div
            v-if="items_list == null || items_list.length === 0"
            class="container"
        >
            <div class="row mb-2">
                <div class="col-12 p-0 bg-warning text-white px-3 py-2 h6">
                    <span>Choose Level before playing</span>
                </div>
            </div>
        </div>
        <div v-else class="container mt-2">
            <div class="row mb-2">
                <div class="col-12 h3 text-center">
                    <span v-text="current_level_item.text"></span>
                </div>
            </div>
            <div class="row mb-2">
                <div class="col-3">Score:</div>
                <div class="col-2 text-end bg-light border border-1">
                    <span
                        :class="`h5
                            ${score >= 0 ? 'text-primary' : 'text-danger'}
                        `"
                        v-text="score"
                    ></span>
                </div>
                <div class="col-1 text-center">
                    <svg
                        width="10"
                        height="20"
                        viewBox="0 0 10 20"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M0 0H10V7.85C10 8.23333 9.91667 8.575 9.75 8.875C9.58333 9.175 9.35 9.41667 9.05 9.6L5.5 11.7L6.2 14H10L6.9 16.2L8.1 20L5 17.65L1.9 20L3.1 16.2L0 14H3.8L4.5 11.7L0.95 9.6C0.65 9.41667 0.416667 9.175 0.25 8.875C0.0833333 8.575 0 8.23333 0 7.85V0ZM2 2V7.85L4 9.05V2H2ZM8 2H6V9.05L8 7.85V2Z"
                            fill="#1C1B1F"
                        />
                    </svg>
                </div>
            </div>

            <div class="row mb-2">
                <div class="col-12 p-0">
                    <textarea
                        class="form-control"
                        rows="2"
                        readonly
                        v-text="quiz.question"
                    ></textarea>
                </div>
            </div>

            <div class="row mb-2">
                <div class="col-5 p-0">
                    <span
                        type="button"
                        :class="`btn rounded-pill btn-sm
                            ${
                                furigana_shown_flag
                                    ? 'btn-outline-primary'
                                    : 'btn-outline-secondary'
                            }
                        `"
                        @click="furigana_shown_flag = !furigana_shown_flag"
                    >
                        <i
                            :class="`fa-solid fa-eye${
                                furigana_shown_flag ? '' : '-slash'
                            }`"
                        ></i>
                        Furigana
                    </span>
                </div>
            </div>

            <div class="row mb-2" v-if="furigana_shown_flag">
                <div class="col-12 p-0">
                    <textarea
                        class="form-control"
                        rows="2"
                        readonly
                        v-text="quiz.furigana"
                    ></textarea>
                </div>
            </div>

            <div class="row mb-2">
                <div class="col-12 p-0 d-grid gap-2">
                    <div
                        :class="`btn rounded-pill px-3 py-1 text-start
                            ${
                                option.checked
                                    ? 'btn-outline-primary disabled'
                                    : 'btn-outline-secondary'
                            }
                            ${option.label ? '' : 'd-none'}
                        `"
                        v-for="(option, option_index) in quiz.options"
                        :key="option_index"
                        @click="checkAnswerOption(option)"
                        :disabled="!option.enabled"
                    >
                        <span v-html="option.label" class=""></span>
                        <span
                            v-if="!option.enabled && option.correct"
                            class="text-success float-end"
                            ><i class="fa-solid fa-circle-check"></i
                        ></span>
                    </div>
                </div>
            </div>

            <div class="row mb-2">
                <div class="col-12 p-0 text-end">
                    <button
                        class="btn btn-primary btn-lg"
                        @click="next"
                        :disabled="!quiz.selected"
                    >
                        &nbsp;&nbsp;<i class="fa-solid fa-angle-right"></i
                        >&nbsp;&nbsp;
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import LocalStorage from "@/utils/LocalStorage.js";
import VocabulariesTopics from "@/data/japanese/index.js";

export default {
    components: {},

    props: {
        model: {
            type: Object,
        },
        tab_index: {
            type: String,
        },
        // localStorage: {
        //     type: Object,
        // },
    },

    data() {
        return {
            score: 0,
            furigana_shown_flag: true,
            quiz: {
                index: 0,
                question: "",
                furigana: "",
                selected: false,
                options: [
                    {
                        label: "",
                        correct: false,
                        checked: false,
                        enabled: true,
                    },
                ],
            },

            topics: null,
            items_list: null,
            selected_index: null,
        };
    },
    computed: {
        current_level_item: {
            get() {
                if (this.localStorage.setting.level.sub_level) {
                    let level = this.localStorage.setting.level;
                    // let topic = level.topic;

                    let result = {
                        level,
                        // key: level_value,
                        text:
                            level.topic +
                            " > " +
                            level.level +
                            " > " +
                            level.sub_level,
                    };

                    return result;
                }
                return null;
            },
        },
        quizIndex: {
            get() {
                return this.quiz.index ?? 0;
            },
            set(value) {
                if (value < 0) {
                    value = 0;
                }
                if (value >= this.items_list.length) {
                    value = this.items_list.length - 1;
                }

                let vocabulary = this.items_list[value];
                this.quiz = {
                    index: value,
                    question: vocabulary.word,
                    furigana: vocabulary.pronunciation,
                    selected: false,
                    options: [
                        {
                            label: vocabulary.meaning ?? vocabulary.example,
                            correct: true,
                            checked: false,
                            enabled: true,
                        },
                    ],
                };
                this.generateRandomFalseOptions();
                this.optionsRandomPosition();
            },
        },
    },

    watch: {
        // "localStorage.setting.level.sub_level": function () {
        //     if (this.current_level_item) {
        //         this.init();
        //     } else {
        //         this.items_list = [];
        //     }
        // },
    },

    methods: {
        next() {
            this.quizIndex = this.getRandomInt(this.items_list.length);
        },
        checkAnswerOption(selected_option) {
            if (!selected_option.enabled) {
                // alert("Bạn đã chọn đáp án này rồi!")
                return;
            }
            selected_option.checked = true;
            this.quiz.selected = true;
            selected_option.enabled = false;

            this.score = selected_option.correct
                ? this.score + 1
                : this.score - 1;
        },

        async init() {
            this.localStorage = new LocalStorage();
            this.localStorage.setLanguageKey("japanese-vietnamese");

            if (this.current_level_item) {
                this.topics = VocabulariesTopics.vietnamese;

                this.items_list =
                    this.topics[this.localStorage.setting.level.topic].value[
                        this.localStorage.setting.level.level
                    ].value[this.localStorage.setting.level.sub_level].value;
            }

            this.quizIndex = await this.getRandomInt(this.items_list.length);
        },

        getRandomInt(max) {
            return Math.floor(Math.random() * Math.floor(max));
        },
        generateRandomFalseOptions() {
            let iBreak = 0;
            for (let i = 0; i < 3; i++) {
                iBreak++;
                if (iBreak > 20) {
                    break;
                }
                let index = this.getRandomInt(this.items_list.length);
                if (index == this.quizIndex) {
                    i--;
                    continue;
                }

                let vocabulary = this.items_list[index];
                this.quiz.options.push({
                    label:
                        vocabulary.meaning == null || vocabulary.meaning == ""
                            ? vocabulary.example
                            : vocabulary.meaning,
                    correct: false,
                    checked: false,
                    enabled: true,
                });
            }
        },
        optionsRandomPosition() {
            this.quiz.options.sort(() => Math.random() - 0.5);
        },
    },
    async created() {
        await this.init();
    },
};
</script>
